/* src/App.css */

body{
  background-color: aqua;
}
.div1 {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color:antiquewhite;
}
h1{
  color: rgb(255, 0, 0);
}
.task-list {
  list-style-type: none;
  padding: 0;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.task-item.completed span {
  text-decoration: line-through;
}

input[type="text"] {
  padding: 8px;
  width: 70%;
  margin-right: 10px;
}

button {
  padding: 5px 10px;
  margin-left: 5px;
  border: none;
  border-radius: 4px;
  background-color: #ff0000;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
